$main_black: #1D1D1B;
$main_yellow: #FFB21B;
$btn-active: #ED9C0C;
$bg_white: #f8f8f8;

@media screen and ( max-width: 1250px ) {
    .news-item {
        &__article {
            padding-bottom: 50px;
            border-bottom: 1px solid #c4c4c480; }
        &__body {
            grid-template-columns: repeat(1, 1fr); }
        &__news {
            grid-template-columns: repeat(3, 1fr); } }

    .about {
        &__container {
            align-items: flex-start; }
        &__content {
            margin-left: 70px; }
        &__bandito {
            &-wrap {
                margin-left: 30px; } } }

    .benefit {
        &:nth-child(2) {
            background-size: 100% 100%; } }

    .slide {
        padding-top: 150px;
        &__image {
            right: -210px;
            &-bg {
                img {
                    width: 80%; } }
            &-item {
                right: 163px; } } } }

@media screen and ( max-width: 1150px ) {

    .section {
        &__title {
            &_big {
                font-size: 40px; } } }

    .game {
        &__name {
            font-size: 24px;
            line-height: 38px; }
        &__img {
            height: 200px; } }

    .game:first-of-type {
        grid-column: 1 / 4;
        .game__img {
            max-height: 490px; } }

    .contacts {
        &__container {
            max-width: 46%; } }

    .game-page {
        &__picset {
            grid-template-rows: 340px 95px; } }

    .slide {
        &__title {
            &, & * {
                font-size: 80px;
                line-height: 96px; } }
        &__image {} }

    .benefit {
        &__title {
            font-size: 22px;
            line-height: 30px; } } }

@media screen and ( max-width: 1050px ) {
    .about-page {
        &__text {
            line-height: 160%;
            &_medium {
                font-size: 20px; }
            &_small {
                font-size: 16px; }
            &_big {
                font-size: 30px;
                line-height: 140%; } } }

    .slide {
        &__image {
            &:nth-child(3) .slide__image-item {
                right: 30px; }
            &:nth-child(4) .slide__image-item {
                right: 80px; }
            &-item {
                right: 51px;
                img {
                    width: 80%;
                    max-width: 100%; } } } }

    .about {
        padding-top: 110px;
        padding-bottom: 95px;
        &__bandito {
            width: 390px;
            height: 400px;
            &-wrap {
                &::before {
                    height: 473px;
                    width: 473px; } } } }

    .news__link {
        font-size: 20px;
        line-height: 26px; }

    .game-page {
        &__top {
            grid-gap: 30px; }
        &__description-text {
            font-size: 20px;
            line-height: 34px; } }

    .games {
        &__container {
            grid-template-rows: repeat(2, 160px); } }

    .games-page {
        &__container {
            grid-gap: 70px 30px; } }

    .benefits {
        padding-bottom: 70px; }

    .game:first-of-type .game__img {
        max-height: 455px; }

    .bandito {
        padding: 30px 5px;
        &__item + &__item {
            margin-top: 23px; } } }

@media screen and ( max-width: 980px ) {
    .header__wrap {
        & > .nav, & > .button_request {
            display: none; } }

    .news-page {
        &__container {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 50px 40px; }
        &.active {
            .news__item:nth-child(n+7) {
                display: block; } }
        .news__item:nth-child(n+7) {
            display: none; } }

    .news__item-img {
        height: 160px; }

    .logo {
        max-width: 95px; }

    .menu {
        display: block !important;
        &__container {
            margin: 80px auto 10px;
            // padding-bottom: 10px !important
            justify-content: flex-start;
            .button_request {
                margin-bottom: 30px; } } }

    .burger {
        display: inline-block; } }

@media screen and ( max-width: 940px ) {
    .about-page__img_float {
        margin-left: 30px; }

    .games-page {
        &__container {
            grid-template-columns: repeat(2, 1fr); } }

    .games {
        &__container {
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(2, 180px); }
        &__item:first-child {
            grid-row: 1 / 2; }
        &_index {
            .games {
                &__container {
                    grid-template-rows: 1fr repeat(2, 215px);
                    grid-template-columns: repeat(2, 1fr); }
                &__item:first-child {
                    grid-column: 1 / 3; } } } }

    .game {
        &__img {
            height: 245px; } }

    .game:first-of-type {
        grid-column: 1 / 3; }

    .about {
        &__container {
            align-items: center;
            flex-direction: column; }
        &__content {
            margin-left: 0px;
            max-width: none;
            margin-top: 60px; }

        &__bandito {
            &-wrap {
                margin-left: 0; } } } }

@media screen and ( max-width: 910px ) {
    .news {
        &__container {
            grid-template-columns: repeat(2, 1fr);
            justify-items: center;
            .news__item {
                max-width: 380px; } } } }

@media screen and ( max-width: 880px ) {
    .about-page {
        &__bottom {
            margin-top: 44px;
            flex-direction: column; }
        &__partnership {
            margin-left: 0;
            margin-right: 0;
            margin-top: 44px;
            .button {
                display: block;
                margin: 40px auto 0px; } }
        &__list {
            max-height: 160px;
            columns: 2; }
        &__text {
            line-height: 160%;
            &_medium {
                font-size: 20px; }
            &_small {
                font-size: 16px; }
            &_big {
                font-size: 30px;
                line-height: 140%; } }
        &__img {
            width: 100%;
            &_float {
                margin: 15px 0px 30px; }
            &:not(.about-page__img_float) {
                max-height: 390px; } } }

    .slide {
        padding-top: 130px;
        padding-bottom: 40px;
        &__images {
            bottom: 50px;
            right: -320px; }
        &__image {
            &:nth-child(3) .slide__image-item {
                right: 231px; }
            &:nth-child(4) .slide__image-item {
                right: 380px; }
            &:nth-child(5) .slide__image-item {
                right: 250px; }
            &-item {
                right: 274px;
                img {
                    width: 60%; } }
            &-bg img {
                width: 48%;
                right: 20%; } }
        &__title {
            line-height: 77px;
            font-size: 77px;
            max-width: 400px; }
        &__data {
            display: flex;
            align-items: flex-end;
            justify-content: space-between; }
        &__names {
            margin-top: 60px; }
        &__controls {
            margin-top: 0;
            margin-bottom: 20px; } }

    .news-item {
        &__news {
            grid-template-columns: repeat(2, 1fr); } }

    .game-page {
        &__description-text {
            line-height: 160%; }
        &__top {
            grid-template-columns: 1fr;
            grid-gap: 25px; }
        &__title {
            font-size: 28px;
            line-height: 160%; }
        &__picset {
            order: 2;
            grid-template-columns: 1fr 125px;
            grid-template-rows: repeat(4, 1fr);
            grid-gap: 16px; }
        &__content {
            grid-gap: 40px; }
        &__preview {
            position: relative;
            width: 100%;
            height: 100%; }
        &__img {
            grid-column: 1 / 2;
            grid-row: 1 / 5;
            max-height: none; }
        &__key, &__value {
            font-size: 19px; }
        &__info, &__text {
            .button {
                margin-top: 0;
                margin-bottom: 40px;
                max-width: none; } } }

    .contacts {
        &__bg {
            opacity: 0; }
        &__container {
            max-width: none; } }

    .benefits {
        &__container {
            flex-direction: column;
            align-items: center; } }

    .benefit {
        width: 100%;
        max-width: 700px;
        &, &:nth-child(2) {
            height: 135px;
            margin: 0;
            background-size: 100% 200%;
            background-position: center; } } }

@media screen and ( max-width: 820px ) {

    .bandito {
        padding: 30px 5px; }

    .footer {
        &__copyright {
            position: absolute;
            left: 0;
            bottom: -20px;
            transform: translateY(100%); }
        .logo {
            margin-right: 20px; } } }

@media screen and ( max-width: 770px ) {
    .popup {
        &__row:not(.popup__row_vert) {
            flex-direction: column;
            align-items: stretch; }
        &__col {
            & + & {
                margin-top: 20px;
                margin-left: 0; } } }

    .games {
        &__container {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(3, 215px); } }

    .game {
        &__img {
            height: 215px; } }

    .slide {
        &__content {
            margin-bottom: 30px; }
        &__data {
            display: block; }
        &__bg {
            display: none; }
        &__controls {
            margin-top: 35px; }
        &__names {
            margin-top: 40px; }
        &__images {
            position: static;
            height: 400px; }
        &__image {
            position: absolute;
            width: 100%;
            height: 400px;
            left: 0;
            bottom: auto;
            &:nth-child(2):not(.active) {
                .slide__image-item {
                    transform: translate(130%, 34px); } }
            &:nth-child(2) {
                .slide__image-item {
                    overflow: hidden;
                    transform: translate(0, 0px);
                    img {
                        transform: translate(0, 34px); } } }
            &:nth-child(3):not(.active) {
                .slide__image-item {
                    transform: translate(130%, 34px); } }
            &:nth-child(3) {
                .slide__image-item {
                    overflow: hidden;
                    transform: translate(0, 0px);
                    img {
                        transform: translate(0, 34px); } } }
            &-mobile {
                display: block; }
            &-item {
                position: absolute;
                transform: translate(0, 4px);
                right: 0 !important;
                width: auto;
                max-width: 90%;
                img {
                    width: auto;
                    max-height: 480px; } }
            &-bg {
                display: none; } } } }

@media screen and ( max-width: 710px ) {
    .about-page {
        &__list {
            max-height: none;
            columns: 1; } }

    .menu__container {
        margin: 80px auto 10px; } }

@media screen and ( max-width: 680px ) {
    .main:not(.main_index) {
        padding-top: 20px; }

    .popup {
        &__form {
            margin-top: 20px; }
        &__title {
            &_thx {
                text-align: center;
                font-size: 28px !important;
                line-height: 44px; }
            &_request {
                font-size: 24px !important;
                margin-right: 30px;
                padding-left: 40px;
                &::before {
                    transform: translateY(0px);
                    position: absolute;
                    left: 0; } } }
        &_request {
            padding: 20px; }
        &_thx {
            padding: 15px;
            display: flex;
            flex-direction: column; }
        &__close {
            top: 23px; }
        &__col {
            .contacts__input_textarea {
                min-height: 95px; } }
        &__row {
            &_vert {
                margin-top: auto;
                height: 60%;
                .button {
                    margin-top: auto; } }
            .button {
                width: 100%;
                max-width: none; } }
        &__overlay {
            padding: 0;
            align-items: stretch;
            justify-content: flex-start; } }

    .section {
        &__title {
            &_page {
                font-size: 32px;
                line-height: 160%; } } }

    .game:first-of-type {
        grid-column: 1 / 2;
        .game {
            &__body {
                background: none;
                padding: 0; }
            &__img {
                max-height: 230px; } } }

    .about-page {
        &__text {
            line-height: 160%;
            &_medium {
                font-size: 18px; }
            &_small {
                font-size: 16px; }
            &_big {
                font-size: 24px;
                line-height: 160%; } } }

    .game {
        max-width: 400px;
        &__img {
            height: 250px !important;
            border-radius: 8px; }
        &:first-of-type {
            .game {
                &__body {
                    position: static; }
                &__body {
                    margin-top: 20px; }
                &__name {
                    font-size: 24px;
                    font-weight: 400;
                    line-height: 38px;
                    margin-bottom: 5px;
                    color: $main_black; }
                &__description {
                    font-size: 16px;
                    line-height: 25px;
                    font-weight: 400;
                    color: $main_black; } } } }

    .games-page {
        &__container {
            grid-gap: 32px;
            justify-items: center;
            grid-template-rows: auto;
            grid-template-columns: repeat(1, 1fr); } }

    .game-page {
        &__content {
            grid-template-columns: 1fr;
            grid-gap: 30px;
            margin-bottom: 0; }
        &__picset {
            grid-template-rows: 470px repeat(2, 130px);
            grid-template-columns: repeat(2, 1fr); }
        &__img {
            width: calc( 100% + 40px );
            grid-column: 1 / 3;
            grid-row: 1 / 2;
            transform: translateX(-20px); }
        &__description-text {
            font-size: 18px;
            line-height: 28px; }
        &__title {
            font-size: 24px;
            line-height: 34px; }
        &__key, &__value {
            font-size: 19px; } }

    .games {
        &__item {
            width: 100%;
            max-width: 400px; }
        &.games_index {
            .games {
                &__item {
                    &:first-child {
                        grid-column: 1 / 2; } }
                &__container {
                    justify-items: center;
                    grid-template-columns: repeat(2, 1fr);
                    grid-template-rows: repeat(3, 1fr); } } }

        &:not(.games_index) {
            &__container {
                justify-items: center;
                grid-template-columns: repeat(1, 1fr);
                grid-template-rows: repeat(5, 255px); } } } }

@media screen and ( max-width: 590px ) {
    .section__title {
        font-size: 32px;
        line-height: 130%; }

    .news-page {
        &__container {
            grid-template-columns: repeat(1, 1fr);
            justify-items: center;
            grid-gap: 40px; } }

    .contacts {
        padding-top: 120px;
        &__subtitle {
            font-size: 18px;
            line-height: 28px; }
        .button {
            max-width: none;
            width: 100%; } }

    .menu {
        &__container {
            justify-content: flex-start;
            .nav__item + .nav__item {
                margin-top: 30px; }
            .nav__link {
                font-size: 38px;
                line-height: normal; } } }

    .section {
        &__top {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 30px; }
        &__more {
            margin-top: 5px; } }

    .about {
        &__text {
            font-size: 16px;
            line-height: 25px; }
        .section__title {
            margin-bottom: 10px; } }

    .news {
        &-item {
            &__body {
                margin-top: 20px; }
            &__img {
                margin-bottom: 20px; }
            &__subtitle {
                font-size: 18px; }
            &__news {
                grid-template-columns: repeat(1, 1fr);
                justify-items: center; } }
        &__item {
            max-width: 450px;
            &-img {
                height: 200px; } } }

    .news__container {
        grid-template-columns: repeat(1, 1fr);
        .news {
            &__item {
                max-width: 380px;
                &-img {
                    height: 160px; } } } }

    .game-page {
        &__picset {
            grid-template-rows: 400px repeat(2, 130px); } }

    .slide {
        &__images {
            height: 350px; }
        &__image {
            height: 350px;
            &:nth-child(2) .slide__image-item {
                img {
                    max-height: 440px;
                    height: 100%; } }
            &:nth-child(3) .slide__image-item {
                img {
                    max-height: 440px;
                    height: 100%; } }
            &-item {
                img {
                    max-height: 380px; } } } } }

@media screen and ( max-width: 470px ) {
    .game-page {
        &__picset {
            grid-template-rows: 270px repeat(2, 100px); } }

    .bandito {
        padding: 20px 0px;
        &_blocked {
            height: 350px !important;
            background-size: contain; }
        &__col {
            padding: 0px 10px; }
        &__item {
            & + & {
                margin-top: 18px; } }
        svg {
            height: 70%;
            width: 70%; } }

    .games {
        .games__container {
            grid-template-columns: repeat(1, 1fr) !important; } }

    .about {
        &__container {
            display: block; }
        &__bandito {
            height: auto;
            width: 100%;
            &-wrap {
                margin-left: auto;
                margin-right: auto;
                width: 100%;
                max-width: 390px; } } }

    .footer {
        &__wrap {
            flex-wrap: wrap; }
        .nav {
            margin-top: 20px;
            &__item {
                &:first-child .nav__link {
                    padding-left: 0; }
                &:last-child .nav__link {
                    padding-right: 0; } } } }

    .header:not(.menu-opened) {
        &__wrap {
            padding-top: 20px; } }

    .slide {
        &__images {
            height: 300px; }
        &__image {
            height: 300px;
            &:nth-child(2) .slide__image-item {
                img {
                    max-height: 370px;
                    width: 100%;
                    height: 100%; } }
            &:nth-child(3) .slide__image-item {
                img {
                    max-height: 370px;
                    width: 100%;
                    height: 100%; } }
            &:nth-child(4) .slide__image-item {
                img {
                    max-height: 330px;
                    height: 100%; } }
            &:nth-child(5) .slide__image-item {
                max-width: 100%;
                img {
                    width: 110%;
                    max-height: 320px;
                    height: 100%; } }
            &-item {
                img {
                    max-height: 380px; } } } } }

@media screen and ( max-width: 440px ) {
    .menu {
        &__container {
            margin-top: 80px;
            .button {
                margin-top: auto; } } }

    .games {
        &__container {
            grid-template-rows: repeat(5, 205px); } }

    .benefit {
        &__title {
            font-size: 20px; }
        &, &:nth-child(2) {
            height: 230px;
            background-size: 100% 100%; } }

    .game {
        &__img {
            max-height: 200px !important; } } }

@media screen and ( max-width: 365px ) {
    .game-page {
        &__picset {
            grid-template-rows: 240px repeat(2, 100px); } }

    .menu {
        &__container {
            width: calc( 100% - 10px ); }
        .button_request {
            padding: 18px 0px; } }

    .slide__title {
        &, & * {
            font-size: 70px;
            line-height: 70px; } } }
